m<template>
  <div>
    <h1> </h1>
    <div>
      <v-btn @click="toastTest1"> Info</v-btn>
      <v-btn @click="toastTest2"> success</v-btn>
      <v-btn @click="toastTest3"> error</v-btn>
      <v-btn @click="toastTest4"> warning</v-btn>
      <v-btn @click="toastTest5"> test</v-btn>
    </div>
    <h1> </h1>
    <div>
      <v-btn @click="progressTest1">START</v-btn>
      <v-btn @click="progressTest2">FINISH</v-btn>
      <v-btn @click="progressTest3">FAIL</v-btn>
    </div>
    <h1>Notify </h1>
    <div>
      <v-btn @click="notifyTest1">Alert</v-btn>
      <v-btn @click="notifyTest2">Confirm</v-btn>
      <v-btn @click="notifyTest3">Prompt</v-btn>
    </div>
    <h1>Axios </h1>
    <div>
      <v-btn @click="axiosTest1">Test</v-btn>
      <v-btn @click="axiosTest2">Error Test</v-btn>
    </div>

    <h1>Socket </h1>
    <div>
      <v-btn @click="joinRoom">  </v-btn>
      <v-btn @click="leaveRoom">  </v-btn>
      <v-btn @click="SendMsg"> </v-btn>
      <div>{{ $store.state.config.test1 }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      title: "My App",
    };
  },

  title() {
    return this.title;
  },

  mounted() {
    //socketio  
    this.$socket.on("room:msg", (data) => {
      console.log("room:msg", data);
    });
  },

  destroyed() {
    //socketio  
    this.$socket.off("room:msg");
  },

  methods: {
    toastTest1() {
      this.$toast.info(" .");
    },
    toastTest2() {
      this.$toast.success("success.");
    },
    toastTest3() {
      this.$toast.error("error.");
    },
    toastTest4() {
      this.$toast.warning("warning.");
    },
    toastTest5() {
      throw new Error(" !");
    },
    progressTest1() {
      this.$Progress.start();
    },
    progressTest2() {
      this.$Progress.finish();
    },
    progressTest3() {
      this.$Progress.fail();
    },
    async notifyTest1() {
      const res = await this.$ezNotify.alert("", "", {
        icon: "mdi-alert",
      });
      console.log(res);
    },
    async notifyTest2() {
      const res = await this.$ezNotify.confirm(" .");
      console.log(res);
    },
    async notifyTest3() {
      const res = await this.$ezNotify.prompt(
        " ?",
        " ?",
        { icon: "mdi-alert", text: " " }
      );
      console.log(res);
    },
    async axiosTest1() {
      const result = await this.$axios.get("/api/member/test");
      console.log(result);
    },
    async axiosTest2() {
      const result = await this.$axios.get("/api/err/test");
      console.log(result);
    },

    //  
    joinRoom() {
      this.$socket.emit("room:join", "roomtest");
      console.log("joinRoom");
    },
    //  
    leaveRoom() {
      this.$socket.emit("room:leave", "roomtest");
      console.log("leaveRoom");
    },
    //  
    SendMsg() {
      this.$socket.emit("room:send", { msg: " " });
    },
  },
};
</script>
