const rules = {
	require({label}) {
		return v => !!v || `[${label}]  .`;
	},
	min({label, len=3}){
		return v => !!v ? v.length >= len || `[${label}] ${len}  .` : true;
	},
	alphaNum() {
		return v => !!v ? /^[a-zA-Z0-9_]+$/.test(v) || `  .` : true; 
	},
	pattern({label, pattern}) {
		return v => !!v ? pattern.test(v) || `[${label}]   .` : true; 
	},
	matchValue(origin) {
		return v => origin === v || '  .';
	},
	id(options) {
		const defaultOptions = {
			label : '',
			len : 3,
			info : null,
			required : true,
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.min(opt));
		return arr;
	},

	name(options) {
		const defaultOptions = {
			label : '',
			len : 2,
			info : null,
			required : true,
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.min(opt));
		return arr;
	},
	password(options){
		const defaultOptions = {
			label : '',
			info : null,
			required : true,
			len : 6,
			//
			//(?=^.{6,}$) 6 
			//(?=.*\d)   
			//(?=.*[a-zA-z])   
			pattern: /^.*(?=^.{6,}$)(?=.*\d)(?=.*[a-zA-z]).*/
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.min(opt))
		arr.push(rules.pattern(opt));
		return arr;
	},
	email(options) {
		const defaultOptions = {
			label : '',
			len : 3,
			info : null,
			required : true,
			pattern: /.+@.+\..+/
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.pattern(opt));
		return arr;
	},
	//pattern 4-2-2
	date(options) {
		const defaultOptions = {
			label : '',
			info : 'YYYY-MM-DD   ',
			required : true,
			pattern: /^\d{4}-\d{2}-\d{2}$/
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.pattern(opt));
		return arr;
	},
	//
	phone(options) {
		const defaultOptions = {
			label : '',
			required : true,
			pattern: /^(\d{2,4}-)?\d{3,4}-\d{4}$/
		};
		const opt = Object.assign(defaultOptions, options);
		const arr = [];
		if(opt.required) {
			arr.push(rules.require(opt));
		}
		arr.push(rules.pattern(opt));
		return arr;
	}
};

module.exports = rules;